import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {StorageManagerProvider} from "../../providers/storage-manager/storage-manager";
import {ApiProvider} from "../../providers/api/api";
import {AuthManagerProvider} from "../../providers/auth-manager/auth-manager";
import {GuiManagerProvider} from "../../providers/gui-manager/gui-manager";
import {MessagesProvider} from "../../providers/messages/messages";
import {OtpProvider} from "../../providers/otp/otp";

/**
 * Generated class for the UserSettingsCreateAccountPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-settings-create-account',
  templateUrl: 'user-settings-create-account.html',
})
export class UserSettingsCreateAccountPage {

  private name = "";
  private medical = false;


  private prename = '';
  private mainname = '';
  private insurance = '';
  private cMedical = '';
  private shouldSendMail = false;
  private targetMail = '';

  private userGroup = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storageManager: StorageManagerProvider,
              private http: ApiProvider, private authManager: AuthManagerProvider, private guiManager: GuiManagerProvider, private messages:MessagesProvider, private otp: OtpProvider) {
    storageManager.fetch("save", (response) => {

      this.name = response.name;
      this.medical = response.medical;
    })
  }

  /**
   * Generates an activation code for a new account (it can be accessed only through the backend at the moment)
   */
  generateCode() {

    console.log("Usergroup: ", this.userGroup);
    const data = {prename: this.prename, name: this.mainname, medical: this.cMedical, mail: this.shouldSendMail ? this.targetMail : "no", userGroup: this.userGroup};
    data["p-type"] = "00P";
    data["insurance-num"] = this.insurance;
    this.http.post('/register', data, [], (success, data) => {

      if (data.success) {
        this.guiManager.showToast("Registrierungscode generiert", 6500);
      } else {
        this.guiManager.showToast("Fehler beim generieren", 6500);

      }

    }, this.authManager.key, true);


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserSettingsCreateAccountPage');
  }

}



